@import "primer-core/index.scss";
@import "primer-marketing/index.scss";
@import "primer-blankslate/index.scss";
@import "primer-tooltips/index.scss";
@import "primer-avatars/index.scss";
@import "primer-popover/index.scss";
@import "primer-layout/index.scss";
@import "leaflet.scss";
@import "explore.scss";
@import "story.scss";

header {
  position: relative;
  z-index: 9998;
}

.blockquote {
  display: block;
  font-size: 80%;
  color: #6c757d;
}

.w-100 {
  width: 100%;
  min-width: 100%;
}

.w-60 {
  width: 60%;
  min-width: 60%;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  margin-bottom: 36px;
  padding: 2px 16px;
  transition: 0.3s;
  overflow: hidden;
}
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.circle {
  border-radius: 50%;
}

.hex-layout {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  width: 190px;
  margin: 0 auto;
  margin-top: 10px;
  .hex {
    width: 33.333%;
    margin-top: -10px;
    text-align: center;
  }
  .hex:nth-child(5n),
  .hex:nth-child(5n - 1) {
    width: 50%;
  }
  .hex:nth-child(5n),
  .hex:nth-child(5n - 2) {
    text-align: left;
  }
  .hex:nth-child(5n - 1),
  .hex:nth-child(5n - 4) {
    text-align: right;
  }
}

.search {
  position: absolute;
  z-index: 9997;
  top: 36px;
  width: 250px;
  right: 0;
  border-radius: 3px;
  border: 1px solid #e1e4e8;
  right: 0;
  padding-bottom: 0;
  p {
    color: #24292e;
  }
  .border-bottom:hover {
    background-color: #efefef;
  }
}

#search-result .column {
  margin: 0;
  text-align: left;
}

#search-result a svg {
  width: 15px;
  margin: 0 10px;
  vertical-align: middle;
}

#search-result a .jumpto {
  visibility: hidden;
  text-align: right;
}

#search-result a:hover .jumpto {
  visibility: visible;
}

@media only screen and (max-width: 1011px) and (min-width: 382px) {
  .search {
    z-index: 9999;
    position: fixed;
    top: 111px;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
  }
}

@media only screen and (max-width: 381px) {
  .search {
    z-index: 9999;
    position: fixed;
    top: 135px;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
  }
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
  margin-bottom: 60px;
  z-index: 99;
}

.post-container {
  max-width: 740px;
  margin-left: auto;
  margin-right: auto;
}

@media only screen and (max-width: 740px) {
  .post-container {
    max-width: 95%;
  }
  .Popover {
    left: 50px;
  }
  .Popover-message {
    width: 250px;
  }
}

$primary-black: rgba(0, 0, 0, 0.84);
$secondary-black: rgba(0, 0, 0, 0.54);
$link-color-blue: rgba(27, 60, 247, 1);
.post {
  color: $primary-black;
  font-size: 21px;
  h1 {
    font-size: 42px !important;
    font-weight: 600 !important;
    line-height: 1.04;
  }
  h2 {
    margin-top: 30px;
    font-size: 38px;
    line-height: 1.15;
  }
  h3 {
    margin-top: 30px;
    font-size: 34px;
    line-height: 1.15;
  }
  img {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  .date {
    margin-top: 10px;
    color: $secondary-black;
  }
  p {
    margin-top: 30px;
    font-size: 21px;
    line-height: 1.58;
    strong {
      font-weight: 700;
    }
  }
  ul {
    margin-top: 30px;
    list-style-position: inside;
    li {
      margin-bottom: 14px;
      margin-left: 30px;
    }
  }
  a {
    text-decoration: none;
    background-image: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.68) 50%,
      rgba(0, 0, 0, 0) 50%
    );
    background-repeat: repeat-x;
    background-size: 2px 0.1em;
    background-position: 0 1.3em;
    color: $link-color-blue;
  }
  code {
    font-size: 18px;
    background-color: rgba(0, 0, 0, 0.05);
    padding: 3px 4px;
    margin: 0 2px;
  }
  img {
    width: 100%;
    margin: 50px auto;
  }
  pre {
    margin-bottom: 30px;
  }
  code {
    margin: 0 !important;
    line-height: 30px !important;
  }
  blockquote {
    border-left: 3px solid #d71e3c;
    p {
      margin: 0px;
      padding: 25px 5px 25px 20px;
      color: #6c757d;
      font-style: italic;
    }
  }
}

.post-profile {
  font-size: 16px;
  margin-bottom: 50px;
  p {
    margin-top: 0px;
    margin-bottom: 0px;
  }
  .avatar {
    width: 50px;
    border-radius: 100% !important;
    margin-right: 15px;
  }
  .profile-link {
    cursor: pointer;
    color: $primary-black;
  }
  .profile-link:active,
  .profile-link:focus,
  .profile-link:hover {
    text-decoration: underline;
  }
  .role {
    color: $secondary-black;
  }
  .profile-info-container {
    display: flex;
  }
}

.popover-mouseover {
  visibility: hidden;
  margin-top: -220px;
}

.expert-profile {
  display: flex;
  flex-direction: column;

  &-info {
    align-items: center;
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: center;
  }
}

.socials-row {
  gap: 8px;
}

.social-logo {
  width: 20px;
  opacity: 0.35;
}
